<template>
  <div class="register--container">
    <div class="container--title">
      <img class="title--img" src="https://om536p71r.qnssl.com/%E9%93%B6%E8%A1%8C%20%281%29.png">
      <span class="title--text"></span>
    </div>
    <div class="input-group--container">
      <div class="input--container">
        <input class="input--el" placeholder="11" type="text" v-model="phoneInput">
        <span class="input--icon"><i class="iconfont">&#xe6d7;</i></span>
      </div>
      <div class="input--container">
        <input class="input--el" placeholder="" type="password" v-model="passwordInput">
        <span class="input--icon"><i class="iconfont">&#xe65e;</i></span>
        <div class="right-btn--area">
          <router-link to="ForgetPassword">
            <div class="global--button_primary radius"></div>
          </router-link>
        </div>
      </div>
      <div class="input--container">
        <div class="right-text">
        <span><router-link to="Register" ></router-link></span>
        </div>
      </div>
      <div class="center-btn--area">
        <div @click="loginHandler" class="global--button_primary radius"></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        phoneInput: '',
        passwordInput: '',
      };
    },
    mounted() {
    },
    methods: {
      loginHandler() {
        this.axios.get(`user/login?phone=${this.phoneInput}&password=${this.passwordInput}`)
            .then((res) => {
              console.log(res.data);
              if (res.code === 1) {
                window.localStorage.checkLogin = true;
                this.$router.replace({
                  name: 'Borrow',
                });
              } else if (res.code === 301) {
                alert('');
              }
                else if (res.code === 302) {
                  alert('');
                }
            });
      },
    },
  };
</script>



