<template>
  <div class="borrow--container">
    <div class="header--content">
        <span class="text"></span>
    </div>
    <div class="table--container">
      <section class="table-cell--content">
        <img class="cell--icon" src="https://om536p71r.qnssl.com/%E6%94%AF%E4%BB%98%E6%96%B9%E5%BC%8F.png" alt="">
        <input class="right-part" type="number" v-model="borrow" min="1" max="100000" placeholder="()"></input>
      </section>
      <section class="table-cell--content">
        <img class="cell--icon" src="https://om536p71r.qnssl.com/%E6%94%AF%E4%BB%98%E6%96%B9%E5%BC%8F%20%281%29.png" alt="">
        <select class="selection" v-model="type">
          <option value =""></option>
          <option value =""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </section>
    </div>
    <div class="center-btn--area">
      <div @click="submitHandler" class="global--button_primary radius"></div>
    </div>
    <div class="popup-dialog--container"  :class="dialogStatus ? 'active' : ''">
      <div class="mask"></div>
      <div class="dialog--main-container">
        <div class="dialog--header"></div>
        <div class="dialog--body">
          <i class="iconfont body--icon">&#xe675;</i>
          <div class="body--desc"></div>
        </div>
        <div class="dialog--footer">
          <div @click="confirmHandler" class="global--button_primary"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        dialogStatus: false,
        borrow: '',
        type: '',
      };
    },
    mounted() {
    },
    methods: {
      confirmHandler() {
        this.dialogStatus = false;
      },
      submitHandler(){
        this.axios.get(`bill/borrow?borrow=${this.borrow}&type=${this.type}`)
            .then((res) => {
                console.log(res);
              if(res.code === 1){
                this.dialogStatus = true;
              } else {
                alert('');
              }
            });
      }
    },
  };
</script>



